import React from "react";
import {ImgHolder} from './general/style/style';

class ImageBanner extends React.Component {


  render() {
    return (
        <ImgHolder height="200px" width="auto" margin="20px auto auto" >
           <img src={this.props.imageUrl} alt={this.props.altText} style={{"height":"100%","width":"auto","margin":"auto"}} />
         </ImgHolder>
    );
  }
}

export default ImageBanner;
